import React from 'react'
import { Link } from 'react-router-dom';
import './style.css'

export default function ItemMovie(props) {
    const { movie } = props;
    return (
        <><div className="page-content mb-5">
            <div className="entry-top d-flex">
                <div className="entry-poster poster-cardmovie amy-movie-item-poster">
                    <Link to={`/detail/${movie.ma_phim}`}>
                        <img src={movie && movie.hinh_anh} alt='' />
                    </Link>

                </div>

                <div className="entry-info">
                    <h1 className="entry-title p-name">
                        <a href="http://demo.amytheme.com/movie/demo/elementor-book-tickets/amy_movie/kubo-and-the-two-strings/" className="u-url url">
                            {movie && movie.ten_phim}
                        </a>
                    </h1>
                    <ul className="info-list">
                        <li>
                            <label>
                                Actor:
                            </label>
                            <span>
                            {movie && movie.dien_vien}
                            </span>
                        </li>
                        <li>
                            <label>
                                Director:
                            </label>
                            <span>
                            {movie && movie.dao_dien}
                            </span>
                        </li>
                        <li>
                            <label>
                                Genre:
                            </label>
                            <span>
                            {movie&&movie.the_loai}
                            </span>
                        </li>
                        <li>
                            <label>
                                Release:
                            </label>
                            <span>
                            {movie && movie.ngay_khoi_chieu.slice(0,10)}
                            </span>
                        </li>
                        <li>
                            <label>
                                Language:
                            </label>
                            <span>
                            {movie&&movie.quoc_gia}
                            </span>
                        </li>
                        <li>
                            <label>
                                Rating:
                            </label>
                            <span>
                            {movie && movie.danh_gia}
                            </span>
                        </li>
                    </ul>
                    <Link to={`/booking/${movie && movie.ma_phim}`}>
                        <button className='btn btn-primary text-left booking'>BOOKING</button>
                    </Link>
                </div>
            </div>
        </div></>
    )
}
